<template>
    <div>
        <van-nav-bar title="后勤维修待分配"/>
        <van-cell-group v-for="(item,index) in wait_list" :key="index">
            <van-cell  :value="item.repair_date" :label="item.room_repair_type" is-link :to="{path:'/logisticsdistribute',query:{fid:item.fid}}">
                <template #title>
                    <span class="custom-title">{{item.person_name}}</span>
                    <van-tag type="primary">{{item.area}}</van-tag>
                </template>
            </van-cell>
        </van-cell-group>     
    </div>  
</template>

<script>
import {apiwaitdistribute_hqlist} from "@/api/index"
export default {
    name:'waitDistribute_hq',
    data() {
        return {
           wait_list:[],
           repair_show:false,
        }
    },
    methods: {
        getlist(){
            var _this=this
            apiwaitdistribute_hqlist().then(res=>{
                console.log(res.data)
                _this.wait_list=res.data.data;
            })
        }
    },
    created(){
       this.getlist();
    }
}
</script>

<style>
.van-cell__title{flex: 2;}
</style>


